@media screen and (max-width: 1023px) {
	.videoChatModal {
		.modalWrapper {
			.tittle {
				width: 80%;
			}
		}
	}
}

@media screen and (max-width: 750px) {
	.videoChatModal {
		.modalWrapper {
			.description {
				min-width: 80%;
			}
		}
	}
}
@media screen and (max-width: 425px) {
	.videoChatModal {
		.modalWrapper {
			.tittle {
				h2 {
					font-size: clamp(15px, 5vw, 20px);
				}
			}
		}
	}
}
